import { ChooseProductComponent } from './edit/choose-product/choose-product.component';
 
import { FormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PriceComparisonComponent } from './price-comparison.component';
import { EditComponent } from './edit/edit.component';

const routes: Routes = [
  {
    path: '',
    component: PriceComparisonComponent
  }, 
  {
    path: 'edit',
    component: EditComponent
  },
]

@NgModule({
  declarations: [
    PriceComparisonComponent,EditComponent,ChooseProductComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule
  ]
})
export class PriceComparisonModule { }
