import { VhAlgorithm, VhQuerySales, VhEcommerce } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from '../../../../../../../../services/language.service';
import { Component, OnInit, ChangeDetectorRef, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-choose-product',
  templateUrl: './choose-product.component.html',
  styleUrls: ['./choose-product.component.scss']
})
export class ChooseProductComponent implements OnInit {
  listShop: Array<any> = [] // danh sách những của hàng lazada đã kết nối
  @Input() showDrawerSelectProduct: boolean
  @Input() listProductShow
  @Output() hideDrawerSelectProduct = new EventEmitter<any>()
  @Input() id_shop: any
  listProduct: Array<any> = []
  listLocalProduct: Array<any> = []
  listSubProduct: Array<any> = []
  allChecked = false;
  indeterminate = false;
  heightScroll: any;
  id_shop_selected: string = '' //id shop đã chọn
  infoShop: any = {}
  setHeight = { "max-height": "500px", "height": "500px" }
  loadingDetail: boolean = false;
  sortPrice: 'asc' | 'desc' = 'asc'
  listPrice: any = {}
  searchValue: any;
  constructor(
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhEcommerce: VhEcommerce,
    private cdRef: ChangeDetectorRef,
  ) { }

  ngOnInit(): void {
    this.listShop = this.vhEcommerce.getlocalShopListTiktok();
  }

  ngOnChanges({ listProductShow }: SimpleChanges): void {
    this.syncProductTiktok();

  }

  /**Chọn shop và lọc sản phẩm theo shop đó */
  changeShop(id) {
    this.listProduct = [];
    this.listSubProduct = []; 
      // this.id_shop = id
      this.infoShop = this.vhEcommerce.getlocalShopListTiktok().find(item => item._id == id)
      this.syncProductTiktok() 
  }
 

  /**
   * Đem sp con ra đứng độc lập, mục đích là dễ sort
   */
  syncProductTiktok() {
    this.listProduct = []
    
    this.vhEcommerce.getlocalProductListTiktok(this.infoShop['_id']).forEach(item => {
      if (item.item_status == 'NORMAL' ) {
        let data = {
          has_model: item.has_model, item_id: item.item_id, item_name: item.item_name,status: false,links:[{href:'',price:null, seller: null, status :null }],
          item_sku: item.item_sku, update_time: new Date(item.update_time * 1000), image: item.image 
        }
        if (item.model && item.model.length) {
          if ((this.listProductShow.findIndex(i => i.model_id == item.model_id) == -1)) item.model.forEach(model => this.listProduct.push({
            ...data, ...model,
            price: model.price_info[0].original_price,   editPrice : 0, minPrice:model.price_info[0].original_price
          }))
        }
        else
        if ((this.listProductShow.findIndex(i => i.item_id == item.item_id) == -1)) this.listProduct.push({
            ...data,
            price: item.price_info[0].original_price, editPrice : 0, minPrice:item.price_info[0].original_price
          })
      }
    })
    this.listSubProduct = this.listProduct ;
    
    
  }
 
  trackByFn(index, item) {
    return index;
  }
  addProduct() {
    this.hideDrawerSelectProduct.emit({ hide: false, productlist: this.listProduct.filter(item => item.status), id_shop: this.id_shop_selected.toString() })
  }

  refreshStatus(): void {
    let validData = this.listProduct
    let allChecked = validData.length > 0 && validData.every(value => value.status === true);
    let allUnChecked = validData.every(value => !value.status);
    this.allChecked = allChecked;
    this.indeterminate = !allChecked && !allUnChecked;
  }

  checkAll(value: boolean): void {
    this.listProduct.forEach(data => {
      data.status = value;
    });
    this.refreshStatus();
  }

  ngAfterViewChecked() {
    if (document.querySelector("#by-product-lazada-price") && document.querySelector(".ant-table-thead") && document.querySelector(".by-product-lazada-price-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#by-product-lazada-price").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".by-product-lazada-price-header").clientHeight - 90 + "px";
    }
    this.cdRef.detectChanges();
  }
  searchProduct(value) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.listProduct = this.vhAlgorithm.searchList(val, [...this.listSubProduct], ['item_name'])
    } else this.listProduct = [...this.listSubProduct];
  }

  hide() {
    this.hideDrawerSelectProduct.emit({ hide: false, productlist: [], id_shop: this.id_shop ? this.id_shop : '' })
  }
}
